.form{
    display: flex;
    justify-content: center;
    color: white;
    margin: 150px ;
    /*border:1px solid white ;*/
}
.main_container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 640px;
    border:1px solid rgba(255, 255, 255, 0.56);
    background: rgba(139, 114, 166, 0.75);
    border-radius: 5px;
    -webkit-box-shadow: -15px 11px 14px -3px rgba(137, 87, 155, 0.6);
    -moz-box-shadow: -15px 11px 14px -3px rgba(137, 87, 155, 0.6);
    box-shadow: -15px 11px 14px -3px rgba(137, 87, 155, 0.6);
}
.input_form{
    width: 520px;
    margin: 0 20px 5px 20px;
    /*border:1px solid #ffcc00;*/
}
.input_form input {
    height: 1.5rem;
    width: 490px;
    border-radius: 5px;
    margin-right: 5px;
}
.span_error {
    color: darkred;
}

.box_container{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 75px;
    width: 200px;
    /*border: 1px solid red;*/
}

.btn {
    font-size: 14px;
    background-color: rgb(6, 79, 131);
    color: aliceblue;
    border-radius: 5px;
    margin-bottom: 5px;
    padding: 10px;
    border: 0 solid #056767;
    box-shadow: inset 0 0 20px rgba(255, 255, 255, 0);
    outline: 1px solid;
    outline-color: rgba(255, 255, 255, .5);
    outline-offset: 0px;
    text-shadow: none;
    transition: all 1250ms cubic-bezier(0.19, 1, 0.22, 1);
}
.btn_disabled{
    background-color: rgb(64, 63, 80);
    font-size: 14px;
    color: aliceblue;
    border-radius: 5px;
    margin-bottom: 5px;
    padding: 10px;
    border: 0 solid #056767;
    box-shadow: inset 0 0 20px rgba(255, 255, 255, 0);
    outline: 1px solid;
    outline-color: rgba(255, 255, 255, .5);
    outline-offset: 0px;
    text-shadow: none;
    /*transition: all 1250ms cubic-bezier(0.19, 1, 0.22, 1);*/
}

.btn:hover {
    border: 1px solid;
    box-shadow: inset 0 0 20px rgba(255, 255, 255, .5), 0 0 20px rgba(255, 255, 255, .2);
    outline-color: rgba(255, 255, 255, 0);
    outline-offset: 15px;
    text-shadow: 1px 1px 2px #074949;
}

.btn:active {
    color: #ebedf1;
    background: #d95e5e;
}
